/* Hourglass Spinner */
.Friends_lds-hourglass__2qTAK {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.Friends_lds-hourglass__2qTAK:after {
    content: " ";
    display: block;
    border-radius: 50%;
    width: 0;
    height: 0;
    margin: 8px;
    box-sizing: border-box;
    border: 32px solid #445572;
    border-color: #445572 transparent #445572 transparent;
    -webkit-animation: Friends_lds-hourglass__2qTAK 1.2s infinite;
            animation: Friends_lds-hourglass__2qTAK 1.2s infinite;
}

@-webkit-keyframes Friends_lds-hourglass__2qTAK {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
        -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
                animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
        -webkit-transform: rotate(900deg);
                transform: rotate(900deg);
        -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
                animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
        -webkit-transform: rotate(1800deg);
                transform: rotate(1800deg);
    }
}

@keyframes Friends_lds-hourglass__2qTAK {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
        -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
                animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
        -webkit-transform: rotate(900deg);
                transform: rotate(900deg);
        -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
                animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
        -webkit-transform: rotate(1800deg);
                transform: rotate(1800deg);
    }
}

